.datetime-picker-wrapper {
  display: flex;
  justify-content: space-around;
  width: 800px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 180px;
}
#datetime-picker {
  width: 100%;
  height: 50px;
  margin-right: 10px;
  border-radius: 4px;
  font-size: 30px;
}
.datetime-picker-button {
  width: 200px;
  font-size: 30px;
}
.timer {
  display: flex;
  justify-content: space-between;
  width: 800px;
  margin-left: auto;
  margin-right: auto;
}
.field {
  text-align: center;
}
.value {
  font-size: 120px;
  font-weight: 500;
}
.label {
  display: block;
  font-size: 50px;
}
